/* ...........Basma........... */
.editTitle {
  font-size: 35px !important;
  font-family: SF med !important;
}
.categoryTitle {
  font-size: 25px !important;
  font-family: SF bold !important;
  color: #cd8930;
  width: 1.5em !important;
  padding-bottom: 4px !important;
  border-bottom: 3px solid rgba(205, 137, 48, 1);
}
.infoTabsUl {
  border: none !important;
  margin-left: -1.5em !important;
}

.tabBtn {
  color: #1e4274 !important;

  /* padding: 0 !important;
  margin: 1em !important; */
  margin-bottom: 1em !important;
  border: none !important;
}
.tabBtn:hover {
  color: #1e4274 !important;
  border: none !important;
}
.tabBtn:after {
  display: block;
  content: "";
  /* transform: scaleX(1); */
  transform: scale(1, 1);
  border: none !important;
  transition: transform 300ms ease-in-out;
}
.tabBtn:hover:after {
  /* transform: scaleX(0); */
  transform: scale(0.5, 1);
  border-bottom: 2px solid #1e4274 !important;
}
.tabBtn.active {
  color: #cd8930 !important;
  border: none !important;
}
.tabBtn.active:after {
  display: block;
  content: "";
  /* transform: scaleX(1); */
  transform: scale(1, 1);
  border: none !important;
  transition: transform 300ms ease-in-out;
  border-bottom: 2px solid #cd8930 !important;
}

@media (max-width: 768px) {
  .editTitle {
    font-size: 25px !important;
  }
  .categoryTitle {
    font-size: 15px !important;
  }
  .infoTabsUl {
    overflow-x: auto;
    overflow-y: hidden;
    flex-wrap: nowrap;
    /* overflow: auto; */
    white-space: nowrap;
  }
  .infoTabsUl::-webkit-scrollbar {
    /* width: 2px !important; */
    height: 2px !important;
  }
  .infoTabs {
    margin-bottom: -1rem !important;
    display: inline-block;
    white-space: nowrap;
  }
  .tabBtn:hover {
    color: #cd8930 !important;
  }
  .tabBtn:hover:after {
    color: #cd8930 !important;
    border-bottom: none !important;
  }
  .tabBtn.active:after {
    border-bottom: none !important;
  }
}
/* General Form */
.editInput {
  background-color: #fafafa;
  border: none !important;
  box-shadow: none;
}
.editInput:focus {
  border: 1px solid #e0e0e0 !important;
  outline: 0 !important;
  box-shadow: none !important;
  outline: 0 !important;
}

.editInput[type="date"]::-webkit-calendar-picker-indicator {
  background: url("https://api.iconify.design/uil:calender.svg?color=%231e4274") no-repeat
    center center / contain;
}
.editInput[type="month"]::-webkit-calendar-picker-indicator {
  background: url("https://api.iconify.design/uil:calender.svg?color=%231e4274") no-repeat
    center center / contain;
}
.editLabel {
  font-size: 1.1rem !important;
  font-family: SF light !important;
}

.hrSkills {
  /* width: 953px; */
  color: #e0e0e0;
  margin-bottom: 0 !important;
}
.editInput[type="number"]::-webkit-outer-spin-button,
.editInput[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
.editInput[type="number"] {
  -moz-appearance: textfield;
}
.editInput[type="radio"] {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 15%;
  outline: none;
  padding-right: 5px;
  border-bottom: none !important;
}

.editInput[type="radio"]:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  padding-right: 5px;
  border-radius: 15%;
  border: 2px solid #1e4274;
}

.editInput[type="radio"]:checked:before {
  background: #1e4274;
  background-image: url(../Components/assests/imgs/done2.svg);
  background-color: #1e4274;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid #1e4274;
}

.raioLabelEdit {
  margin-left: 10px !important;
  margin-bottom: 0px !important;
  padding-bottom: 0px !important;
}
::placeholder {
  color: rgba(30, 66, 116, 0.7) !important;
